import React,{Component} from 'react';
import './book.scss';

class Book extends Component{
    constructor(){
        super()
        this.state = {
            bookList:[
                {
                    logo:"static/img/a.jpg",
                    title:"微信公众号前端实践",
                    author:"月翎魂雨",
                    chapter:"14",
                    saleTotal:120,
                    price:"9.9"
                },
                {
                    logo:"static/img/b.jpg",
                    title:"小程序实战",
                    author:"月翎魂雨",
                    chapter:"10",
                    saleTotal:90,
                    price:"9.9"
                },
                {
                    logo:"static/img/c.jpg",
                    title:"angular搭建管理后台",
                    author:"月翎魂雨",
                    chapter:"20",
                    saleTotal:77,
                    price:"9.9"
                },
                {
                    logo:"static/img/d.jpg",
                    title:"vue实现音乐播放器",
                    author:"月翎魂雨",
                    chapter:"21",
                    saleTotal:120,
                    price:"9.9"
                },
                {
                    logo:"static/img/e.jpg",
                    title:"react搭建视频播放器",
                    author:"月翎魂雨",
                    chapter:"65",
                    saleTotal:120,
                    price:"9.9"
                },
                {
                    logo:"static/img/f.jpg",
                    title:"webpack 搭建自己的脚手架",
                    author:"月翎魂雨",
                    chapter:"111",
                    saleTotal:120,
                    price:"9.9"
                },
            ]
        }
    }
    render(){
        const listItem = this.state.bookList.map((book,index)=>
            <li
                className="book-item"
                key={index}
            >
                <img src={book.logo} />
                <div className="book-des">
                    <h3>{book.title}</h3>
                    <span className="book-author">{book.author}</span>
                    <span
                        className="total-calc"
                    >{parseInt(book.chapter)}小节 . {parseInt(book.saleTotal)}人已赞助</span>
                </div>
                <div className="book-price">￥{parseFloat(book.price)}</div>
            </li>
        );
        return(
           <div className="book-container">
                <ul>
                    {listItem}
                </ul>
           </div>
        )
    }
}
export default Book;